import React, { Component, Fragment } from 'react';
// import PropTypes from 'prop-types';


// const propTypes = {

// };


class FormSignUp extends Component {

	state = {
		txtEmail: '',
		txtUsername: '',
		txtPassword: '',
		txtFullname: ''
	}

	_onChange = e => {
		const target = e.target;
		let name = target.name;
		let value = target.value;

		this.setState({
			[name]: value
		})
	}

	_onSubmit = e => {
		e.preventDefault();
		const { txtEmail, txtUsername, txtPassword, txtFullname } = this.state;

		if(txtEmail && (txtUsername.length >= 8) && (txtPassword.length >= 8) && txtFullname){
			this.props.register(txtEmail, txtUsername, txtPassword, txtFullname);
			// console.log(this.props)
		}
	}

	render() {
		const { txtEmail, txtUsername, txtPassword, txtFullname } = this.state;
		// console.log({ txtEmail, txtUsername, txtPassword, txtFullname });
		return (
			<Fragment>
				<form className="form-horizontal m-t-30" onSubmit={this._onSubmit}>
					<div className="form-group">
						<label htmlFor="useremail">Email</label>
						<input
							type="email"
							className="form-control"
							id="useremail"
							placeholder="Enter email"
							name='txtEmail'
							value={txtEmail}
							onChange={this._onChange} />
					</div>
					<div className="form-group">
						<label htmlFor="username">Username</label>
						<input
							type="text"
							className="form-control"
							id="username"
							placeholder="Enter username"
							name='txtUsername'
							value={txtUsername}
							onChange={this._onChange} />
					</div>
					<div className="form-group">
						<label htmlFor="userpassword">Password</label>
						<input
							type="password"
							className="form-control"
							id="userpassword"
							placeholder="Enter password"
							name='txtPassword'
							value={txtPassword}
							onChange={this._onChange} />
					</div>
					<div className="form-group">
						<label htmlFor="fullname">FullName</label>
						<input
							type="text"
							className="form-control"
							id="fullname"
							placeholder="Enter fullname"
							name='txtFullname'
							value={txtFullname}
							onChange={this._onChange} />
					</div>
					<div className="form-group row m-t-20">
						<div className="col-12 text-right">
							<button className="btn btn-primary w-md waves-effect waves-light" type="submit">Register</button>
						</div>
					</div>
					<div className="form-group m-t-10 mb-0 row">
						<div className="col-12 m-t-20">
							{/* eslint-disable-next-line */}
							<p className="font-14 text-muted mb-0">By registering you agree to the Lexa <a href="#" className="text-primary">Terms of Use</a></p>
						</div>
					</div>
				</form>
			</Fragment>
		);
	}
}


// FormSignUp.propTypes = propTypes;


export default FormSignUp;
