class HeaderSignUp extends Component {
    render() {
        return (
            <Fragment>
                <div className="card">
                    <div className="card-body">
                        <h3 className="text-center m-0"><a href="index.html" className="logo logo-admin"><img src="assets/images/logo.png" height={30} alt="logo" /></a></h3>
                        <div className="p-3">
                            <h4 className="text-muted font-18 m-b-5 text-center">Free Register</h4>
                            <p className="text-muted text-center">Get your free fonik account now.</p>
                            
                            {/* Form SignUp */}
                            {/* {console.log(this.props)} */}
                            {this.props.showForm}
                        </div>
                    </div>
                </div>

            </Fragment>
        );
    }
}


